import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Tipo_notificacao } from '../../../modelo/Tipo_notificacao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'tipo_notificacao-dados-details',   templateUrl: 'tipo_notificacao_dadosDetails.component.html' }) export class Tipo_notificacaoDadosDetailsComponent implements OnInit {    tipo_notificacao: Tipo_notificacao;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.tipo_notificacao.Tipo_notificacao";                 let id = +params['id'];         this.tipo_notificacao = new Tipo_notificacao();         this.tipo_notificacao.cd_tipo_notificacao = id;         o.objetoJson = this.tipo_notificacao;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a tipo_notificacao. '; }      this.tipo_notificacao = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


